.favorite {
  padding: 0 30px 30px 30px;
  height: 100%;

  img {
    border-radius: 10px;
    background-color: #f0f0f0;
  }
  button {
    padding: 10px 40px;
    background-color: #137777;
    color: #fff;
    border: none;
    cursor: pointer;
    margin: 10px;
    border-radius: 10px;
  }
}