.Shop {
  padding: 30px;
  margin: 0 0 30px 0;
  display: flex;
  justify-content: space-around;
  border: 1px solid black;
  overflow: scroll;

  .container {
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
    background-color: #f0f0f0;
    padding: 20px;
    border: 1px solid #ccc;

  }
  button {
    padding: 10px 20px;
    background-color: #137777;
    color: #fff;
    border: none;
    cursor: pointer;
    margin: 10px;
    border-radius: 10px;
  }

  button:hover {
    background-color: #36a4a4;
  }
  .product{
    float: left;
    width:  220px;
    height: 280px;
    object-fit: cover;
  }

}